import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { BroCode } from '../Models/bro.code.model';


@Injectable({
    providedIn: 'root'
})
export class BroCodeServices {

    constructor(
        private http: HttpClient
    ) { }
    getRandom() {
        return this.http.get<any>(environment.apiUrl + "Bro/GetRandom");
    }



}